<template>
  <div class="container">
    <div class="search">
        <div class="input-field">
            <input class="default" v-model="newCity" placeholder="Enter city name">
            <div class="error">{{error}}</div>
        </div>
            <button class="btn btn-search" :disabled="!newCity" @click="search">Search</button>
            <button class="btn add-city" :disabled="!newCity" @click="addCity">Add to favorite</button>
    </div>
    <div class="weather">
        <h3>{{placeName}}, {{country}}</h3>
        <div class="temperature">{{currentTemp}}°</div>
        <div>{{overcast}}</div>
        <div><img :src=icon></div>
        <div>L {{minTemp}}° / H {{maxTemp}}°</div>
        <div> sunrise {{sunrise}} / sunset {{sunset}}</div>
        <div> humidity {{humidity}}</div>
        <div> pressure {{pressure}}</div>
        <div> Wind {{wind}}</div>
    </div>
    <div class="favorite-cities" v-show="cities.length > 0">
        <div class="favorite-city" v-for="(city,n) in cities">
            <div>
                <span class="my-city" @click="seeWeather(n)">{{city}}</span>
                <button class="btn remove-city" @click="removeCity(n)">Remove</button>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
   data() {
    return {
    apiLink:'https://api.openweathermap.org/data/2.5/weather?units=metric',
    apiKey: '&APPID=16f86fb95d29de5b39a40af937274cbe',
    cities:[],
    newCity: null,
    placeName: '',
    country: '',
    currentTemp: '',
    minTemp: '',
    maxTemp:'',
    sunrise: '',
    sunset: '',
    pressure: '',
    humidity: '',
    wind: '',
    overcast: '',
    icon: '',
    error: ''
  }
},
  methods: {
    getWeather(url) {
      axios
        .get(url)
        .then(response => {
            this.error = '';
            this.placeName = response.data.name;
            this.country = response.data.sys.country;
            this.currentTemp = response.data.main.temp;
            this.minTemp = response.data.main.temp_min;
            this.maxTemp = response.data.main.temp_max;
            this.pressure = response.data.main.pressure;
            this.humidity = response.data.main.humidity + '%';
            this.wind = response.data.wind.speed + 'm/s';
            this.overcast = response.data.weather[0].description;
            this.icon = "https://openweathermap.org/img/w/" + response.data.weather[0].icon + ".png";
            this.sunrise = new Date(response.data.sys.sunrise*1000).toLocaleTimeString("uk-UA").slice(0,5);
            this.sunset = new Date(response.data.sys.sunset*1000).toLocaleTimeString("uk-UA").slice(0,5);
        })
        .catch(error => {
            this.error = error.response.data.message;
        });
    },
    search(){
        let url = this.apiLink + '&q=' + this.newCity + this.apiKey;
        this.getWeather(url);
    },
    seeWeather(x){
        let url = this.apiLink + '&q=' + this.cities[x] + this.apiKey;
        this.getWeather(url);
    },
    geolocation() {
      navigator.geolocation.getCurrentPosition(this.buildUrl, this.geoError);
    },
    buildUrl(position) {
      const lat = position.coords.latitude;
      const lon = position.coords.longitude;
      this.getWeather(this.apiLink + '&lat=' + lat + '&lon=' + lon + this.apiKey);
    },
    geoError(error) {
      this.getWeather(this.apiLink + '&lat=0&lon=0' + this.apiKey);
    },
    addCity() {
      // ensure they actually typed something
      if(!this.newCity) return;
      this.cities.push(this.newCity);
      this.newCity = '';
      this.savecities();
    },
    removeCity(x) {
      this.cities.splice(x,1);
      this.savecities();
    },
    savecities() {
      let parsed = JSON.stringify(this.cities);
      localStorage.setItem('cities', parsed);
    }
  },
  beforeMount() {
    this.geolocation();
  },
  mounted(){
     if(localStorage.getItem('cities')) {
      try {
        this.cities = JSON.parse(localStorage.getItem('cities'));
      } catch(e) {
        localStorage.removeItem('cities');
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style> 
body{
    background-image: url(/static/images/bg.jpg);
    background-size: cover;
    min-width: 320px;
    margin: 0;
    height: 100vh;
}
h1, h2 {
  font-weight: normal;
}
h3{
    font-size: 28px;
    margin-top: 15px
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.container{
    max-width: 800px;
    margin: 0px auto;
    padding: 10px;
}
.weather{
    background-image: -webkit-linear-gradient(#2c5fe4,#1d202d);
    background-image: linear-gradient(#2c5fe4,#1d202d);
    color: #fff;
    max-width: 410px;
    margin: 20px auto;
    padding: 10px;
    border-radius: 6px;
}
input.default{
    margin: 5px;
    border: 1px solid #777;
    border-radius: 4px;
    padding: 10px 10px;
    font-size: 16px;
    box-shadow: rgba(0,0,0,.05) 0 5px 20px;
    letter-spacing: 0;
    width: 205px;
    color: #333;
    font-weight: 500;
}
.btn{
    display: inline-flex;
    overflow: hidden;
    margin: 5px;
    padding: 10px 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all 150ms linear;
    text-align: center;
    white-space: nowrap;
    text-decoration: none !important;
    text-transform: none;
    text-transform: capitalize;
    color: #fff;
    border: 0 none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.3;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 5px 10px #e4e4e4;
}
.btn:hover {
    transition: all 150ms linear;
    opacity: .85;
}
.btn:disabled{
    opacity: .65;
    cursor: default;
}
.btn.btn-search{
    background: #416dea;
}
.btn.add-city{
    background: #3dd28d;
}
.btn.remove-city{
    background: #ff0202;
    box-shadow: none;
    float: right;
}
.temperature{
    font-size: 40px;
}
.favorite-cities{
    max-width: 410px;
    margin: 0px auto;
    padding: 10px;
    border: 1px solid #ddd;
    max-height: 200px;
    overflow-y: scroll;
    background-color: #1f4371;
    color: #fff;
}
.my-city{
    cursor: pointer;
    float: left;
    padding: 15px;
}
.favorite-city{
    float: left;
    display: inline-block;
    width: 100%;
}
.input-field{
    display: inline-block;
}
.error{
    color: #ff0202;
    display: table;
    height: 20px;
}
@media (max-width: 500px) {
    .input-field{display: block;}
}
</style>



